import { Component, OnInit } from '@angular/core';
import { routerTransition } from '../../router.animations';
import { Router } from '@angular/router';
import {HttpModule, Http, Response, Headers, RequestOptions } from '@angular/http';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { ApiService } from '../../shared/services/api.service';
import { Customers } from '../../Models/customers.class';
import { AuthService } from '../../auth/auth.service';
import { DataService } from '../../shared/services/data-service.service';
import { CustomersDataObject } from '../../Models/customers.class';
import { DevicesDataObject } from '../../Models/devices.class';

import { AlertComponent } from '../../layout/alert/index';


@Component({
    selector: 'app-control',
    templateUrl: './control.component.html',
    styleUrls: ['./control.component.scss'],
    animations: [routerTransition()]
})
export class ControlComponent implements OnInit {

    loading = false;
    errorMessage;
    tbToken: any = null;
    customerData: CustomersDataObject[];
    devicesData: DevicesDataObject[];
    customerId = localStorage.getItem('customerId');
    type = 'air_sensor';
    deviceToken = '';
    deviceId = '';


    constructor (
        private http: Http,
        private apiService: ApiService,
        private router: Router,
        private _dataService: DataService) { }

    ngOnInit() {

        this.loading = true;
        if (JSON.parse(localStorage.getItem('currentUser')) === null) {
        this.apiService.tbLogin()
        .subscribe(response => {
          if (response === true) {   // login successful
            const tbUser = JSON.parse(localStorage.getItem('currentUser'));
            this.tbToken = tbUser.token;
            console.log('the tbToken is : ===>', this.tbToken);
        }
        },
          (error) => {this.errorMessage = error; this.loading = false; },
          () => {this.loading = false; this.getDevicesByCustomer(); }
        );
          } else {
            this.getDevicesByCustomer();
          }


    }

    getDevicesByCustomer() {
        this.loading = true;
        this.errorMessage = '';
        const tbUser = JSON.parse(localStorage.getItem('currentUser'));
        this.tbToken = tbUser.token;
        this.apiService.getDevicesByCustomer(this.tbToken, this.customerId, this.type)
            .subscribe((data) => {
              this.devicesData = data;
              console.log('Devices list:' , this.devicesData);
            },
            (error) => {this.errorMessage = error; this.loading = false; },
            () => {this.loading = false;
            }
          );
      }
      storeDeviceId(id) {
        localStorage.setItem('deviceId', id);
        this.deviceId = id;
        this.getDeviceToken(id);
      }

      getDeviceToken(deviceId) {
        this.loading = true;
        this.errorMessage = '';
        const tbUser = JSON.parse(localStorage.getItem('currentUser'));
        this.tbToken = tbUser.token;
        this.apiService.getDeviceToken(this.tbToken, deviceId)
            .subscribe((data) => {
              this.devicesData = data;
              this.deviceToken = this.devicesData['credentialsId'];
              localStorage.setItem('deviceToken', this.deviceToken);
              console.log('Devices Token:' , this.deviceToken);
            },
            (error) => {this.errorMessage = error; this.loading = false; },
            () => {this.loading = false;
            }
          );
      }
    }



