import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { StatModule } from '../../shared';
import { ApiService } from '../../shared/services/api.service';


import { ControlRoutingModule } from './control-routing.module';
import { ControlComponent } from './control.component';
import { PageHeaderModule } from './../../shared';
import { HttpModule } from '@angular/http';
import { AuthService } from '../../auth/auth.service';
import {DeviceControlComponent} from './device/device-control.component';
import { FormsModule } from '@angular/forms';

import { AlertComponent } from '../../layout/alert/index';
import { AlertService } from '../../shared/services/alert.service';



@NgModule({
    imports: [CommonModule,
        ControlRoutingModule,
        PageHeaderModule,
        HttpModule,
        FormsModule,
        StatModule
    ],
    schemas: [ CUSTOM_ELEMENTS_SCHEMA ],
    declarations: [ControlComponent, DeviceControlComponent, AlertComponent],
    providers: [AuthService, ApiService, AlertService ]
})
export class ControlModule {}



