import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { routerTransition } from '../../../router.animations';
import { Router } from '@angular/router';
import { HttpModule, Http, Response, Headers, RequestOptions } from '@angular/http';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { ApiService } from '../../../shared/services/api.service';
import { DataService } from '../../../shared/services/data-service.service';
import { DevicesDataObject } from '../../../Models/devices.class';
import { AttributesOject } from '../../../Models/attributes.class';

import { AlertService } from '../../../shared/services/alert.service';

@Component({
    selector: 'app-device-control',
    templateUrl: './device-control.component.html',
    styleUrls: ['./device-control.component.scss']
})
export class DeviceControlComponent implements OnInit {

    loading = false;
    errorMessage;
    tbToken: any = null;
    devicesAttributes: AttributesOject[];
    deviceId = localStorage.getItem('deviceId');
    deviceToken = localStorage.getItem('deviceToken');

    counterValue: number;
    idleValue: number;

    @Output() counterChange = new EventEmitter();
    @Output() idleChange = new EventEmitter();

    constructor (
        private http: Http,
        private apiService: ApiService,
        private router: Router,
        private _dataService: DataService,
        private alertService: AlertService) { }

    ngOnInit() {
        this.getDevicesAttributes();
        console.log('CounterValue: ngOnInit', this.counterValue);
        // this.setTemp()
    }

    getDevicesAttributes() {
        this.loading = true;
        this.errorMessage = '';
        const tbUser = JSON.parse(localStorage.getItem('currentUser'));
        this.tbToken = tbUser.token;
        this.apiService.getAttributesbyDevice(this.tbToken, this.deviceToken)
            .subscribe((data) => {
                this.devicesAttributes = data;
                console.log('Devices Atributes:' , this.devicesAttributes);
                this.counterValue = this.devicesAttributes['settemp'];
                this.idleValue = this.devicesAttributes['setidle'];
                console.log( 'Print CounterValue : ', this.counterValue);
                console.log ('Print idleValue: ', this.idleValue);
            },
            (error) => {
                this.errorMessage = error;
                this.loading = false;
                this.alertService.error('Error');
                 },
            () => {this.loading = false;
            }
            );
        }
        setTemp(value) {
            this.apiService.setAttribute(this.tbToken, this.deviceId, 'settemp', value)
                .subscribe((response) => {
                    const resp = response;
                    if (resp['status'] == 200) {
                        console.log('atributo alterado com sucesso');
                        this.alertService.clear();
                        this.alertService.success('Temperatura atualizada');
                    } else {
                        this.alertService.clear();
                        this.alertService.error('Error: entre em contato com adminsitrador');
                    }
                    console.log ('resposta de setAttribute', response);
                },
            );
          }

        setIdle(value) {
            this.apiService.setAttribute(this.tbToken, this.deviceId, 'setidle', value)
                .subscribe((response) => {
                    const resp = response;
                    if (resp['status'] == 200) {
                        console.log('atributo alterado com sucesso');
                        this.alertService.clear();
                        this.alertService.success('Ociosidade atualizada');
                    } else {
                        this.alertService.clear();
                        this.alertService.error('Error: entre em contato com adminsitrador');
                    }
                    console.log ('resposta de setAttribute', response);
                },
            );
          }


    @Input()
    get counter() {
        return this.counterValue;
    }
    set counter(val) {
        this.counterValue = val;
        this.counterChange.emit(this.counterValue);
    }
    get idlecounter() {
        return this.idleValue;
    }
    set idlecounter(val) {
        this.idleValue = val;
        this.idleChange.emit(this.idleValue);
    }
    decrementTemp () {
        this.counter--;
    }
    decrementIdle() {
        this.idlecounter--;
    }
    incrementTemp() {
        this.counter++;
    }
    incrementIdle() {
        this.idleValue++;
    }


}



